import Searchfetch from '../components/Searchfetch.js';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    BrowserRouter
  } from "react-router-dom";
function Search(){
    return(
        <BrowserRouter>
            <Switch>
                <Route path="/" component={Searchfetch} />
            </Switch>
        </BrowserRouter>
    )
}

export default Search;